body, html {
    height: 100%;
}

body {
    margin: 0;
    font-family: Frutiger, "Frutiger Linotype", Univers, Calibri, "Gill Sans", "Gill Sans MT", "Myriad Pro", Myriad, "DejaVu Sans Condensed", "Liberation Sans", "Nimbus Sans L", Tahoma, Geneva, "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 14px;
}

.bg {
    background: url(../../img/surf/bg.jpg) center;
        background-size: cover;
}

.top {
    position: fixed;
    top: 0;
    bottom: 180px;
    left: 0;
    right: 0;
    z-index: 1;
}

.bottom {
    position: fixed;
    bottom: 0;
    height: 180px;
    left: 0;
    right: 0;
    z-index: 0;
}

.small-footer {
    .top {
        bottom: 130px;
    }

    .bottom {
        height: 130px;
    }

    .footer .chat-menu {
        display: none;
    }
}

.billboard-chain-left, .billboard-chain-right {
    position: absolute;
    top: 0;
    background: url(../../img/surf/bill-chain.png);
    width: 28px;
    height: 49px;
}

.billboard-chain-left {
    left: 10%;
}

.billboard-chain-right {
    right: 10%;
}

.billboard-sign {
    position: absolute;
    top: 30px;
    left: 5%;
    background: url(../../img/surf/bill-sign.png);
    width: 85px;
    height: 85px;
    margin-left: -45px;
}

.billboard, .billboard-shadow, .frame-wrapper {
    width: 90%;
    left: 5%;
    right: 5%;
    position: absolute;
    border-radius: 5px;
    bottom: 49px;
    top: 49px;
    margin-bottom: 50px;
}

.billboard-shadow {
    box-shadow: inset 0 0 43px #000;
    border-radius: 5px;
}

.billboard {
    background: url(../../img/surf/bill-bg2.jpg), url(../../img/surf/bill-bg.jpg);
    background-blend-mode: overlay;
}

.frame-wrapper {
    padding: 40px 40px 0;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

iframe {
    border: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 0 10px #000;
    background: #fff;
    border-radius: 3px;
}

.footer-fence {
    width: 100%;
    height: 49px;
    background: url(../../img/surf/footer-fence.png);
    position: absolute;
    z-index: 2;
    bottom: 0;
}

.footer {
    background: url(../../img/surf/footer-bg2.png), url(../../img/surf/footer-bg.jpg);
    position: absolute;
    height: 100%;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    width: 100%;
    box-shadow: 0 -10px 80px 50px #000;
    background-position: 0;
}

.character {
    position: absolute;
    left: 5%;

    .name {
        position: absolute;
        z-index: 3;
        display: inline-block;
        width: 100%;
        text-align: center;
        bottom: -19px;

        span {
            color: #fff;
            display: inline-block;
            border-radius: 3px;
            background: #816A48;
            padding: 0 5px;
        }
    }

    .message {
        position: absolute;
        z-index: 3;
        display: inline-block;
        width: 100%;
        text-align: center;
        bottom: 100%;

        span {
            color: #fff;
            display: inline-block;
            border-radius: 3px;
            background: #816A48;
            padding: 5px;
            margin-bottom: 3px;
            position: relative;
            box-shadow: 0 0 8px #000;
            max-width: 100%;
            word-wrap: break-word;

            &:after {
                border: solid;
                border-color: #816A48 transparent;
                content: "";
                position: absolute;
                left: 50%;
                margin-left: -5px;
                border-width: 6px 6px 0 6px;
                bottom: -6px;
            }
        }
    }
}

.human {
    width: 127px;
    height: 127px;
    background: url(../../img/surf/human-sheet.png);
    bottom: 9px;
}

.footer .left {
    width: 600px;
    margin: 0;
    float: left;
    padding: 0;
}

.footer .right {
    width: 600px;
    margin: 0;
    float: left;
    padding: 0 0 0 5px;
}

.footer .inner {
    width: 1200px;
    margin: 15px auto auto;
}

.footer .messages {
    padding: 10px;
    height: 100px;
    background: #000;
    border: 1px solid #231b0f;
    border-radius: 5px;
    overflow: auto;
}

.footer .messages .message {
    line-height: 18px;
    color: #baa166;
}

.footer .messages .message span {
    color: #fff;
    margin-right: 5px;
}

.footer .chat-menu {
    margin-top: 5px;
}

.footer .chat-menu .form {
    background: url(../../img/surf/message-form.png);
    background-size: 100% 100%;
    height: 47px;
    width: 510px;
    float: left;
    padding: 8px 10px;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.footer .chat-menu .form .type {
    height: 30px;
    width: 75px;
    background: #161414;
    border-radius: 3px;
    display: inline-block;
    color: #896d48;
    text-align: center;
    line-height: 30px;
    box-shadow: inset 2px 2px 6px #000;
}

.footer .chat-menu .form .message {
    height: 30px;
    width: 405px;
    background: #161414;
    border-radius: 3px;
    display: inline-block;
    color: #896d48;
    padding-left: 10px;
    box-shadow: inset 2px 2px 6px #000;
    border: 0;
    outline: 0;
    float: right;
    padding-top: 2px;
}

.footer .chat-menu .send {
    background: url(../../img/surf/send-button.png);
    height: 47px;
    width: 80px;
    float: right;
    color: #896d48;
    text-align: center;
    line-height: 47px;
    cursor: pointer;

    &:hover {
        background: linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) ), url(../../img/surf/send-button.png);
    }
}

.loading {
    text-align: center;
    font-size: 40px;
    margin-top: 200px;
}

.wrapper {
    display: none;
}

.loader {
  height: 50px;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  text-align: center;
}
.loader .ball, .loader-inner .ball {
  width: 20px;
  height: 20px;
  background-color: #fff;
  border-radius: 50%;
  display: inline-block;
  -webkit-animation: motion 3s cubic-bezier(0.77, 0, 0.175, 1) infinite;
          animation: motion 3s cubic-bezier(0.77, 0, 0.175, 1) infinite;
}

.loader p, .loader-inner p {
  color: #fff;
  margin-top: 5px;
  font-family: sans-serif;
  letter-spacing: 3px;
  font-size: 15px;
}

.loader-inner {
  height: 50px;
  margin: auto;
  text-align: center;
}
.loader-inner .ball {
  background-color: #333;
}

.loader-inner p {
  color: #333;
}

@-webkit-keyframes motion {
  0% {
    -webkit-transform: translateX(0) scale(1);
            transform: translateX(0) scale(1);
  }
  25% {
    -webkit-transform: translateX(-50px) scale(0.3);
            transform: translateX(-50px) scale(0.3);
  }
  50% {
    -webkit-transform: translateX(0) scale(1);
            transform: translateX(0) scale(1);
  }
  75% {
    -webkit-transform: translateX(50px) scale(0.3);
            transform: translateX(50px) scale(0.3);
  }
  100% {
    -webkit-transform: translateX(0) scale(1);
            transform: translateX(0) scale(1);
  }
}

@keyframes motion {
  0% {
    -webkit-transform: translateX(0) scale(1);
            transform: translateX(0) scale(1);
  }
  25% {
    -webkit-transform: translateX(-50px) scale(0.3);
            transform: translateX(-50px) scale(0.3);
  }
  50% {
    -webkit-transform: translateX(0) scale(1);
            transform: translateX(0) scale(1);
  }
  75% {
    -webkit-transform: translateX(50px) scale(0.3);
            transform: translateX(50px) scale(0.3);
  }
  100% {
    -webkit-transform: translateX(0) scale(1);
            transform: translateX(0) scale(1);
  }
}

.sites-view h1 {
    color: #ECECEC;
    text-align: center;
    padding-top: 0;
    padding-bottom: 30px;
    text-shadow: 0 0 10px #606060;
    letter-spacing: 3px;
}

.sites-view .table {
    border-radius: 3px;
    overflow: hidden;
    box-shadow: 0 0 10px #606060;
}

.sites-view .table-striped > tbody > tr:nth-of-type(even) {
    background: #eee;
}

.sites-view .table-striped > thead > tr {
    background: #ADADAD;
}

.sites-view .frame-wrapper {
    overflow: auto;
    margin-bottom: 90px;
    margin-top: 40px;
    padding-top: 0;
}

.right {
    float: right;
}

.margin-right {
    margin-right: 10px;
}

.map-view .items {
    position: absolute;
    bottom: 20px;
    z-index: 10;

    span {
        position: absolute;
        bottom: 0px;
        display: inline-block;
    }

    span::after {
        content: "";
        width: 76%;
        height: 8px;
        -webkit-filter: blur(3px);
        -moz-filter: blur(3px);
        -o-filter: blur(3px);
        -ms-filter: blur(3px);
        filter: blur(3px);
        position: absolute;
        bottom: -10px;
        left: 12%;
        border-radius: 5px;
        background: #fff;
    }

    img {
        max-width: 30px;
        margin-bottom: 15px;
        -webkit-animation: item_float .7s infinite alternate linear;
        animation: item_float .7s infinite alternate linear;
        border-radius: 3px;
    }
}

@-webkit-keyframes item_float {
    0% {
        margin-bottom: 12px;
    }
    100% {
        margin-bottom: 5px;
    }
}

@keyframes item_float {
    0% {
        margin-bottom: 12px;
    }
    100% {
        margin-bottom: 5px;
    }
}

ul.menu {
    list-style: none;
    display: table;
    width: 100%;
    padding: 4px 0 5px;
    border-radius: 3px;
    background: url(../../img/surf/menu.png);
    background-size: 100% 100%;

    li {
        display: table-cell;
        border-right: 2px solid #231F1C;
        line-height: 35px;
        color: #896d48;
        text-align: center;
        text-transform: capitalize;

        &:hover {
            background-color: rgba(0,0,0, .2);
            cursor: pointer;
        }

        &.coins {
            border-right: none;
            background-repeat: no-repeat;
            background-position: right;
            padding-right: 20px;

            .count {
                color: #ccc;
            }

            &::after {
                content: "";
                width: 40px;
                height: 32px;
                background: url(../../img/surf/coins.png);
                display: inline-block;
                position: absolute;
                margin-left: 8px;
                margin-top: -4px;
            }
        }
    }
}
